@font-face {
    font-family: "MyriadProCondBold";
    src: url(../fonts/MyriadProCondBold/MyriadProCondBold.eot);
    src: url(../fonts/MyriadProCondBold/MyriadProCondBold.eot?#iefix);
    src: url(../fonts/MyriadProCondBold/MyriadProCondBold.woff);
    src: url(../fonts/MyriadProCondBold/MyriadProCondBold.ttf);
    font-style: normal;
    font-weight: normal; }

@font-face {
    font-family: MyriadProCondRegular;
    src: url(../fonts/MyriadProCondRegular/MyriadProCondRegular.eot);
    src: url(../fonts/MyriadProCondRegular/MyriadProCondRegular.eot?#iefix);
    src: url(../fonts/MyriadProCondRegular/MyriadProCondRegular.woff);
    src: url(../fonts/MyriadProCondRegular/MyriadProCondRegular.ttf);
    font-style: normal;
    font-weight: normal; }

@font-face {
    font-family: "LC Chalk";
