 left: 47px; }


.feedback {
  position: absolute;
  left: 888px;
  top: 11px;
  width: 116px;
  height: 28px;
  border: none;
  border-radius: 10px;
  background: linear-gradient(to top, #7fa59d, #98c0b8);
  color: white;
  font-family: Arial, sans-serif;
  font-size: 12px;
  font-family: "MyriadProCondRegular";
  cursor: pointer; }


.modal {
   display: none;
   position: fixed;
   z-index: 2;
   left: 0;
   top: 0;
   width: 100%;
   height: 100%;
   overflow: auto;
   background-color: rgba(0,0,0,0.6); }

.modal-content {
   text-align: center;
   background-color: #fefefe;
   margin: 15% auto;
   padding: 20px;
   border: 1px solid #888888;
   width: 40%;
   height: 300px; }

.close {
   color: #aaaaaa;
   float: right;
   font-size: 28px;
   font-size: bold; }


.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer; }







